*,
*:before,
*:after {
  box-sizing: border-box;
  outline: unset;
}

/* ios */
html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer,
header,
nav,
section,
main {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  color: unset;
  text-decoration: none;
}

img {
  width: 100%;
  object-fit: contain;
}

* {
  --bg-main: #171F29;
  --bg-first: #fff;
  --bg-second: #ECDFD1;
  --c-first: #47576C;
  --c-second: #171F29;
  --c-third: #192D47;
  --c-fourth: #A3ABB5;
  --c-fifth: #989899;
  --c-white: #fff;
  --c-hover-first: #171F29;
  --c-hover-second: #304259;
  --c-hover-third: #192D470D;
  --b-radius-first: 12px;
  --b-radius-second: 8px;
  --b-radius-third: 4px;
  --shadow-first: 0px 8px 24px 0px #02031CCC;
  --shadow-hover-first: 0px 8px 20px 0px #02031CCC;
  --anim-offset: 2px;
  --anim4: 0.4s ease-in-out;
  --anim2: 0.2s ease-in-out;
  --anim1: 0.1s ease;
}

html {
  font-family: "Mulish", sans-serif;
  font-size: 16px;
  color: var(--c-first);
  cursor: default;
}

body {
  line-height: 1.5;
}

h1 {
  font-weight: 900;
  font-size: 64px;
  line-height: 1.13;
}

h2 {
  font-weight: 700;
  font-size: 56px;
  line-height: 1.15;
}

h3 {
  font-weight: 700;
  font-size: 48px;
  line-height: 1.17;
}

h4 {
  font-weight: 900;
  font-size: clamp(24px, 5vw, 40px);
  line-height: 1.2;
}

h5 {
  font-weight: 900;
  font-size: 24px;
  line-height: 1.34;
}

h6 {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
}

strong,
b {
  font-weight: 700;
}

.title--first {
  font-weight: 900;
  font-size: clamp(56px, 6vw, 80px);
  line-height: 1.1;
}
@media screen and (max-width: 820px) {
  .title--first {
    font-weight: 700;
  }
}
.title--second {
  font-weight: 900;
  font-size: clamp(48px, 8vw, 64px);
  line-height: 1.17;
}
@media screen and (max-width: 820px) {
  .title--second {
    font-weight: 700;
  }
}
.title--third {
  font-weight: 900;
  font-size: clamp(24px, 5vw, 40px);
  line-height: 1.2;
}
.title--fourth {
  font-weight: 900;
  font-size: clamp(24px, 7vw, 64px);
  line-height: 1.2;
}
.title--sixth {
  font-weight: 700;
  font-size: clamp(16px, 4vw, 24px);
}

.article-page h1,
.article-page h2,
.article-page h3,
.article-page h4,
.article-page h5,
.article-page h6 {
  margin-bottom: clamp(16px, 4vw, 24px);
  color: var(--c-third);
}
.article-page h1 ol,
.article-page h1 ul,
.article-page h2 ol,
.article-page h2 ul,
.article-page h3 ol,
.article-page h3 ul,
.article-page h4 ol,
.article-page h4 ul,
.article-page h5 ol,
.article-page h5 ul,
.article-page h6 ol,
.article-page h6 ul {
  margin-top: 64px;
}
.article-page img {
  max-height: 800px;
}
.article-page .alignleft {
  float: left;
  padding: 24px 24px 24px 0;
  max-width: 400px;
}
.article-page .alignright {
  float: right;
  padding: 24px 0 24px 24px;
  max-width: 400px;
}
.article-page .aligncenter {
  display: block;
  margin: 24px 0;
  text-align: center;
}
.article-page .alignnone {
  padding: 24px 0;
}
.article-page img {
  padding: clamp(40px, 2vw, 64px) 0;
}
.article-page .alignleft,
.article-page .alignright,
.article-page .alignnone {
  max-width: clamp(350px, 28vw, 400px);
}
@media screen and (max-width: 820px) {
  .article-page .alignleft,
.article-page .alignright,
.article-page .alignnone {
    max-width: 100%;
    margin: 0 auto;
    width: 100%;
    padding: 40px 0;
  }
}
.article-page strong {
  font-weight: 700;
}
.article-page em {
  font-style: italic;
}
.article-page ul {
  list-style: disc;
}
.article-page ol {
  list-style: auto;
}
.article-page ul,
.article-page ol {
  max-width: 80%;
  padding: 24px;
}
@media screen and (max-width: 820px) {
  .article-page ul,
.article-page ol {
    max-width: 100%;
  }
}
.article-page ul li:not(:last-child),
.article-page ol li:not(:last-child) {
  margin-bottom: 16px;
}
.article-page ul + h2, .article-page ul + h3, .article-page ul + h4, .article-page ul + h5,
.article-page ol + h2,
.article-page ol + h3,
.article-page ol + h4,
.article-page ol + h5 {
  margin-top: 20px;
}
.article-page p {
  font-weight: 700;
  text-align: justify;
}
@media screen and (max-width: 820px) {
  .article-page p {
    font-weight: 400;
  }
}
.article-page p + p {
  margin-top: clamp(8px, 2vw, 24px);
}
.article-page p + h1, .article-page p + h2, .article-page p + h3, .article-page p + h4, .article-page p + h5, .article-page p + h6 {
  margin-top: clamp(24px, 4vw, 64px);
}
.article-page a {
  text-decoration: underline;
}
@media (hover: hover) {
  .article-page a:hover {
    color: var(--c-hover-first);
  }
}

