.bg {
  color: var(--c-first);
}

section ul li + li,
section ol li + li {
  margin-top: 8px;
}

p + p {
  margin-top: 10px;
}

input {
  border: 1px solid var(--c-fourth);
  border-radius: 2px;
  padding: 13px 16px;
  width: 100%;
  font-family: "Mulish", sans-serif;
  font-size: 16px;
}

button {
  cursor: pointer;
}

header .container,
footer .container,
section .container {
  border-radius: var(--b-radius-first);
}

section {
  color: var(--c-white);
}
section ul,
section ol {
  list-style: auto;
  padding: 16px 20px;
}
section ul {
  list-style: disc;
}
section:first-child {
  --gap: 24px;
  margin-top: calc(var(--offset-header) + var(--gap));
}
@media screen and (max-width: 820px) {
  section:first-child {
    --gap: 16px;
  }
}

.no-scroll {
  overflow-y: hidden;
}

.title {
  color: var(--c-second);
}

.footer--first .container,
.bg--first .container {
  background: var(--bg-first);
}
.footer--second .container,
.bg--second .container {
  background: var(--bg-second);
}

.row {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}

section.our-services + section.team .container {
  padding-top: unset;
}

.card {
  display: flex;
  flex-direction: column;
  border-radius: var(--b-radius-first);
  padding: 24px;
  width: calc(25% - 18px);
  min-height: 432px;
  color: var(--c-first);
}
.card-wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
}
.card__title {
  margin-bottom: 24px;
  height: clamp(56px, -104px + 12.5vw, 96px);
  font-weight: 900;
  font-size: clamp(24px, -40px + 5vw, 40px);
  line-height: 1.2;
  color: var(--c-second);
}
@media screen and (max-width: 820px) {
  .card__title {
    height: unset;
  }
}
.card__btn {
  display: block;
  margin-top: auto;
  margin-left: auto;
  width: min-content;
  font-size: 0;
}
@media screen and (max-width: 820px) {
  .card__btn {
    margin-top: 64px;
  }
}
.card--first {
  background: var(--bg-first);
  transition: var(--anim2);
}
@media (hover: hover) {
  .card--first:hover {
    box-shadow: var(--shadow-hover-first);
  }
}
.card--second {
  background: var(--bg-second);
  transition: var(--anim2);
}
@media (hover: hover) {
  .card--second:hover {
    box-shadow: var(--shadow-hover-first);
  }
}
@media screen and (max-width: 1280px) {
  .card {
    width: calc(50% - 12px);
  }
}
@media screen and (max-width: 820px) {
  .card {
    width: 100%;
    min-height: unset;
  }
}

.pagination {
  display: flex;
  margin: 0 auto;
  width: max-content;
  gap: 8px;
}
.pagination-wrapper .container {
  padding-top: 16px;
  padding-bottom: clamp(56px, 12vw, 96px);
}
.pagination__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--c-white);
  border-radius: 2px;
  width: 44px;
  height: 44px;
  cursor: pointer;
}
.pagination__btn--prev {
  transform: rotate(180deg);
}
.pagination__btn--current {
  font-weight: 700;
  color: var(--c-second);
  background: var(--bg-first);
}
@media (hover: hover) {
  .pagination__btn:hover {
    color: var(--c-second);
