.breadcrumbs {
  display: flex;
  align-items: center;
  list-style: none;
  margin-bottom: 24px;
  padding: unset;
  font-size: clamp(12px, 2vw, 24px);
}
.breadcrumbs a {
  font-weight: 900;
}
.breadcrumbs li {
  margin: unset;
}
.breadcrumbs li:not(:last-child) {
  margin-right: clamp(8px, 2vw, 16px);
}
.breadcrumbs li:not(:last-child)::after {
  content: "/";
  padding-left: clamp(8px, 2vw, 16px);
  font-weight: 500;
}

form {
  font-family: "Mulish", sans-serif;
}
form input {
  border-radius: var(--b-radius-second);
  width: 100%;
}
form label {
  margin-bottom: 8px;
  font-weight: 700;
  color: var(--c-second);
}
form .form_input_wrap {
  display: flex;
  flex-direction: column-reverse;
}

.filter {
  position: relative;
  margin-left: auto;
  padding-bottom: 16px;
  width: max-content;
}
.filter-wrapper .container {
  padding: unset;
}
.filter input {
  border-color: var(--c-fourth);
  color: var(--c-fifth);
  background: transparent;
  transition: var(--anim2);
  cursor: pointer;
}
.filter .btn--dropdown i {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  transform-origin: top;
  filter: brightness(4) grayscale(1);
}
.filter__list {
  position: absolute;
  left: 0;
  right: 0;
  top: calc(100% + var(--anim-offset));
  z-index: 5;
  border-radius: var(--b-radius-second);
  padding: unset;
  box-shadow: var(--shadow-first);
  color: var(--c-second);
  background: var(--bg-first);
  opacity: 0;
  visibility: hidden;
  transition: var(--anim2);
}
.filter__list li {
  display: block;
  margin-top: unset;
  padding: 6px 16px;
  cursor: pointer;
}
@media (hover: hover) {
  .filter__list li:hover {
    background: var(--c-hover-third);
  }
}
.filter__title {
  position: relative;
}
.filter__title i {
  transition: var(--anim2);
}
@media (hover: hover) {
  .filter__title:hover input {
    border-color: var(--c-white);
    color: var(--c-white);
  }
  .filter__title:hover i {
    filter: brightness(100);
  }
