}
.contacts .col {
  display: flex;
  flex-direction: column;
  width: 50%;
  gap: 40px;
}
@media screen and (max-width: 1280px) {
  .contacts .col {
    width: 100%;
  }
}
@media screen and (max-width: 425px) {
  .contacts .col {
    gap: 24px;
  }
}
.contacts iframe {
  width: 100% !important;
  height: 100% !important;
  min-height: clamp(605px, 69vw, 665px);
  filter: invert(1) hue-rotate(180deg);
}
.contacts a {
  display: flex;
  align-items: center;
}
.contacts a img {
  width: 24px;
  height: 24px;
}
.contacts a img + span {
  margin-left: 8px;
}
@media (hover: hover) {
  .contacts a:hover {
    color: var(--c-hover-first);
    opacity: 0.8;
  }
}

.article-list .container {
  display: flex;
  gap: 40px 24px;
  flex-wrap: wrap;
  padding: 40px 0 0;
}
@media screen and (max-width: 820px) {
  .article-list .container {
    gap: 24px;
  }
}
.article__item {
  border-radius: var(--b-radius-first);
  padding: 24px;
  width: calc(33.3333333333% - 16px);
  color: var(--c-first);
  background: var(--c-white);
  transition: var(--anim2);
}
@media screen and (max-width: 1280px) {
  .article__item {
    width: calc(50% - 24px);
  }
}
@media screen and (max-width: 820px) {
  .article__item {
    padding: 16px;
    width: 100%;
  }
}
@media (hover: hover) {
  .article__item:hover {
    box-shadow: var(--shadow-hover-first);
  }
}
.article__item-title {
  font-weight: 900;
  margin-bottom: 16px;
}
.article__item img {
  margin-bottom: 24px;
  border-radius: var(--b-radius-second);
  height: 350px;
  object-fit: cover;
}
.article__item .btn {
  margin-top: 24px;
  margin-left: auto;
}
@media screen and (max-width: 820px) {
  .article__item .btn {
    margin-top: 16px;
  }
}
@media screen and (max-width: 425px) {
  .article__item .btn {
    width: 100%;
    text-align: center;
  }
}
.article-page-wrap {
  max-width: 868px;
  margin: 0 auto;
}
.article-page--second img:not(.alignnone, .alignleft, .alignright, .aligncenter) {
  --offset: clamp(0px, -214.2857px + 21.4286vw, 60px);
  margin-left: calc(var(--offset) * -1);
  margin-right: calc(var(--offset) * -1);
  width: calc(100% + var(--offset) * 2);
}